
import { defineComponent, reactive, toRefs, onMounted } from 'vue'
import { useStore } from 'vuex'

export default defineComponent({
  name: 'ToolBar',
  //组件发出的事件需要定义
  emits: ['handleChangeDesigner', 'handleClearLayout', 'handleDeleteControl'],
  setup(props, { emit }) {
    const store = useStore()

    //数据对象
    let data: any = reactive({
      title: '设计窗口', //工具栏标题
      show_tips: true, //是否显示提示消息
    })

    //挂载事件
    onMounted(() => {
      setTimeout(() => {
        data.show_tips = false //隐藏提示消息
      }, 3000)
    })

    //点击工具栏按钮
    const handleClickToolBtn = (val: number) => {
      switch (val) {
        case 1:
          //原地切换改变显示边框状态
          if (data.title == '设计窗口') {
            store.dispatch('handleChangeShowBorder', !store.state.show_border)
          }
          data.title = '设计窗口'
          emit('handleChangeDesigner', val)
          break
        case 2:
          data.title = '查看代码'
          emit('handleChangeDesigner', val)
          break
        case 3:
          if (data.title == '设计窗口') {
            //清空布局
            emit('handleClearLayout', val)
          }
          break
        case 4:
          if (data.title == '设计窗口') {
            //删除控件
            emit('handleDeleteControl', val)
          }
          break
        default:
          break
      }
    }

    return {
      ...toRefs(data),
      handleClickToolBtn,
    }
  },
})
